.menu-bars{
    width: 4rem;
    position: relative;
    cursor: pointer;
    transition: all .3s ease-in-out;  
    .burguerMenu{
        position: fixed;
        top: 35px;
        width: 4rem;
        height: 3rem;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        z-index: 4;
        span{
            background-color: $fontColor;
            width: 4rem;
            height: .6rem;
            border-radius: .2rem;
            transition: all .5s ease-in-out;                        
        }
        &:hover{
            span{
                background-color: $primario;
            }
        }
          
    }
    #collapse{
        display: none;
    }
    #collapse:checked ~ .burguerMenu #top{
        transform: translateY(12px) rotate(315deg);
        background-color: $fontColor;        
    }
    #collapse:checked ~ .burguerMenu #center{
        opacity: 0;
    }
    #collapse:checked ~ .burguerMenu #bottom{
        transform: translateY(-12px) rotate(-315deg);
        background-color: $fontColor;
    }
    
}
@keyframes fadeOut {
    0%{
        opacity: 1;
        
    }
    100%{
        opacity: 0;
        transform: translateX(-5rem);
        display: none;
    }
}
.navMenu{
    position: fixed;    
    top: 0;
    right: 0;
    background-color: $primario;
    width: 100%;
    height: 100vh;
    display: flex;    
    align-items: center;
    justify-content: center;
    z-index: 2;    
    transition: all .5s ease-in-out;
    overflow: hidden;
    @include tablet{
        width: 75%;
    } 
    @include desktop{
        width: 60%;
    } 
    &.activo{
        clip-path: circle(150% at 100% 0%);     
    }
    &.inactivo{
        clip-path: circle(0% at 100% 0);
    }    
}

ul.links{  
    position: relative;
    width: 100%;
    li {
        text-align: center;
        a {
            font-size: 3rem;
            line-height: 2;
            font-weight: $normal;
            @include telefono{
                font-size: 3.5rem;
                line-height: 1.8;
            }
            @include tablet{
                font-size: 3.5rem;
            }
            @include desktop{
                font-size: 5rem;
            }
            &:before{
                content: attr(data-text);
                text-transform: uppercase;
                font-size: 18vw;
                position: absolute;
                top: 0;
                left: 0;                                
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                color:$primarioOscuro;
                font-weight: $bold;
                z-index: -1;
                opacity: 0;              
                transition: .3s all ease-in-out;
                @include tablet{
                    font-size: 9rem;
                }
                @include desktop{
                    font-size: 11.5rem;
                }                
            }
        }            
        a:hover::before{
            opacity: 1;
        }
            
    }
}
.navMenu.display-none{
    display: none;
}
   