.hero{   
    display: flex;
    flex-direction: column;    
    width: 100%;
    background-color: $blanco;
    overflow: hidden;
    @include tablet{
        height: 100vh;
    }
    @include dosk{
        height: 60vh;
    }
}
.header{     
    width: 100%;
    margin-bottom: $separacion;
    .nav-bar{
        display: flex;
        height: 10rem;
        justify-content: space-between;
        align-items: center;
        padding: 2rem;
        position: relative;
        .logo{
            display: flex;
            width: 8rem;
            @include tablet{
                width: 12rem;
            }
        }        
        .navigation{
            display: flex;
            align-items: center;
            .shop-bag{
                margin-right: 2rem;
                i.fas {                
                    font-size: 4rem;
                    color: #393939;
                    transition: all .3s ease-in-out;
                    position: relative;
                    &:hover{
                        color: $primario;
                    }
                                   
                }
            }
            span.count{                
                position: absolute;                
                top: -.5rem;
                right: 0;
                display: grid;
                place-items: center;                
                width: 1.5rem;
                height: 1.5rem;
                border-radius: 50%;
                font-size: 1.2rem;
                color: #e1e1e1;
                font-weight: $light;                
                background-color: $primarioOscuro;
                transition: all .3s ease-in-out;
                //display: none;
            }
        }
    }
}

.hero-content{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;    
    @include tablet{
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(2, 50%);
        place-items: center;
    }        
    .hero__left{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        z-index: 1;
        @include tablet{
            padding-left: 2rem;
            margin-top: 20rem;
        }
          
        h1{
            font-size: 6rem;
            text-align: center;            
            @include tablet{
                font-size: 10rem;
                text-align: left;
            }
            @include desktop{
                font-size: 12rem;
            }
           
            span {
                display: block;
                font-size: 4.5rem;
                @include tablet{
                    font-size: 7.5rem;
                }
            }
        }
        p.slogan{
            font-size: 2.5rem;
            font-weight: $light;
            text-align: center;
            @include tablet {
                font-size: 3rem;
                text-align: left;
            }
        }
        .button{
            margin: 2rem 0;
            @include tablet{
                margin-top: 8rem;
                width: 80%;
            }
            @include desktop{
                width: 60%;
            }            
        }
    }
    .hero__right{  
        height: 100%;
        width: 100%;
        display: grid;
        place-items: center;      
        img{ 
            @include tablet{
                width: 180%;
                transform: translateX(10rem);
            }
            @include desktop{
                transform: translateX(0);
            }
           
        }
        
    }
}