.container{
    width: 96%;
    max-width: 120rem;
    margin: 0 auto;
}
.no-scroll{
    overflow: hidden;
}
.button{
    position:relative;
    padding: 1.5rem 3rem;
    background-color: $primario;
    color: $grisClaro;
    font-size: 2.5rem;
    text-align: center;
    cursor: pointer;
    transition: all .3s ease-in-out;   
    @include tablet{
        width: 50%;
    }
    &:hover{
        background-color: $primarioOscuro;;
    }    
}
.new-section{
    margin-top: $separacion/2;
}
.titulo{
    text-align: center;
    text-transform: uppercase;
    margin: $separacion/2 0;
}
