.container {
  width: 96%;
  max-width: 120rem;
  margin: 0 auto; }

.no-scroll {
  overflow: hidden; }

.button {
  position: relative;
  padding: 1.5rem 3rem;
  background-color: #76947F;
  color: #f6f6f6;
  font-size: 2.5rem;
  text-align: center;
  cursor: pointer;
  transition: all .3s ease-in-out; }
  @media (min-width: 768px) {
    .button {
      width: 50%; } }
  .button:hover {
    background-color: #63886e; }

.new-section {
  margin-top: 4rem; }

.titulo {
  text-align: center;
  text-transform: uppercase;
  margin: 4rem 0; }

html {
  font-size: 62.5%;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  color: #393939; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  background-color: #fff;
  line-height: 2; }

img {
  width: 100%; }

ul {
  padding: 0;
  margin: 0;
  list-style: none; }

a {
  text-decoration: none;
  color: #393939; }

h1, h2, h3, h4 {
  margin: 0;
  line-height: 1; }

h2 {
  font-size: 3rem; }

h3 {
  font-size: 2.6rem; }

p {
  margin: 0; }

.hero {
