  flex-direction: column;
  width: 100%;
  background-color: #fff;
  overflow: hidden; }
  @media (min-width: 768px) {
    .hero {
      height: 100vh; } }
  @media (min-width: 3840px) {
    .hero {
      height: 60vh; } }

.header {
  width: 100%;
  margin-bottom: 8rem; }
  .header .nav-bar {
    display: flex;
    height: 10rem;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    position: relative; }
    .header .nav-bar .logo {
      display: flex;
      width: 8rem; }
      @media (min-width: 768px) {
        .header .nav-bar .logo {
          width: 12rem; } }
    .header .nav-bar .navigation {
      display: flex;
      align-items: center; }
      .header .nav-bar .navigation .shop-bag {
        margin-right: 2rem; }
        .header .nav-bar .navigation .shop-bag i.fas {
          font-size: 4rem;
          color: #393939;
          transition: all .3s ease-in-out;
          position: relative; }
          .header .nav-bar .navigation .shop-bag i.fas:hover {
            color: #76947F; }
      .header .nav-bar .navigation span.count {
        position: absolute;
        top: -.5rem;
        right: 0;
        display: grid;
        place-items: center;
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 50%;
        font-size: 1.2rem;
        color: #e1e1e1;
        font-weight: 200;
        background-color: #63886e;
        transition: all .3s ease-in-out; }

.hero-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  @media (min-width: 768px) {
    .hero-content {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(2, 50%);
      place-items: center; } }
  .hero-content .hero__left {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    z-index: 1; }
    @media (min-width: 768px) {
      .hero-content .hero__left {
        padding-left: 2rem;
        margin-top: 20rem; } }
    .hero-content .hero__left h1 {
      font-size: 6rem;
      text-align: center; }
      @media (min-width: 768px) {
        .hero-content .hero__left h1 {
          font-size: 10rem;
          text-align: left; } }
      @media (min-width: 998px) {
        .hero-content .hero__left h1 {
          font-size: 12rem; } }
      .hero-content .hero__left h1 span {
        display: block;
        font-size: 4.5rem; }
        @media (min-width: 768px) {
          .hero-content .hero__left h1 span {
            font-size: 7.5rem; } }
    .hero-content .hero__left p.slogan {
      font-size: 2.5rem;
      font-weight: 200;
      text-align: center; }
      @media (min-width: 768px) {
        .hero-content .hero__left p.slogan {
          font-size: 3rem;
          text-align: left; } }
    .hero-content .hero__left .button {
      margin: 2rem 0; }
      @media (min-width: 768px) {
        .hero-content .hero__left .button {
          margin-top: 8rem;
          width: 80%; } }
      @media (min-width: 998px) {
        .hero-content .hero__left .button {
          width: 60%; } }
  .hero-content .hero__right {
    height: 100%;
    width: 100%;
    display: grid;
    place-items: center; }
    @media (min-width: 768px) {
      .hero-content .hero__right img {
        width: 180%;
        transform: translateX(10rem); } }
    @media (min-width: 998px) {
      .hero-content .hero__right img {
        transform: translateX(0); } }

.menu-bars {
  width: 4rem;
  position: relative;
  cursor: pointer;
  transition: all .3s ease-in-out; }
  .menu-bars .burguerMenu {
    position: fixed;
    top: 35px;
    width: 4rem;
    height: 3rem;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 4; }
    .menu-bars .burguerMenu span {
      background-color: #393939;
      width: 4rem;
      height: .6rem;
      border-radius: .2rem;
      transition: all .5s ease-in-out; }
    .menu-bars .burguerMenu:hover span {
      background-color: #76947F; }
  .menu-bars #collapse {
    display: none; }
  .menu-bars #collapse:checked ~ .burguerMenu #top {
    transform: translateY(12px) rotate(315deg);
    background-color: #393939; }
  .menu-bars #collapse:checked ~ .burguerMenu #center {
    opacity: 0; }
  .menu-bars #collapse:checked ~ .burguerMenu #bottom {
    transform: translateY(-12px) rotate(-315deg);
    background-color: #393939; }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0;
    transform: translateX(-5rem);
    display: none; } }

.navMenu {
  position: fixed;
  top: 0;
  right: 0;
  background-color: #76947F;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  transition: all .5s ease-in-out;
  overflow: hidden; }
  @media (min-width: 768px) {
    .navMenu {
      width: 75%; } }
  @media (min-width: 998px) {
    .navMenu {
      width: 60%; } }
  .navMenu.activo {
    clip-path: circle(150% at 100% 0%); }
  .navMenu.inactivo {
    clip-path: circle(0% at 100% 0); }

ul.links {
  position: relative;
  width: 100%; }
  ul.links li {
    text-align: center; }
    ul.links li a {
      font-size: 3rem;
      line-height: 2;
      font-weight: 400; }
      @media (min-width: 480px) {
        ul.links li a {
          font-size: 3.5rem;
          line-height: 1.8; } }
      @media (min-width: 768px) {
        ul.links li a {
          font-size: 3.5rem; } }
      @media (min-width: 998px) {
        ul.links li a {
          font-size: 5rem; } }
      ul.links li a:before {
        content: attr(data-text);
        text-transform: uppercase;
        font-size: 18vw;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
