        justify-content: center;
        align-items: center;
        text-align: center;
        color: #63886e;
        font-weight: 700;
        z-index: -1;
        opacity: 0;
        transition: .3s all ease-in-out; }
        @media (min-width: 768px) {
          ul.links li a:before {
            font-size: 9rem; } }
        @media (min-width: 998px) {
          ul.links li a:before {
            font-size: 11.5rem; } }
    ul.links li a:hover::before {
      opacity: 1; }

.navMenu.display-none {
  display: none; }

.about-content .about-text {
  font-size: 1.8rem;
  text-align: center; }
