html{
    font-size: 62.5%;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
    color: $fontColor;
}
*, *:before, *:after {
    box-sizing: inherit;
  }
  body{
      background-color: $blanco;
      line-height: 2;
  }

  img{
      width: 100%;
  }
  ul {
      padding: 0;
      margin: 0;
      list-style: none;
  }
  a {
      text-decoration: none;
      color: $fontColor;
  }
  h1, h2, h3, h4 {
      margin: 0;
      line-height: 1;
  }
  h2{
      font-size: 3rem;
  }
  h3{
      font-size: 2.6rem;
  }
  p{
      margin: 0;
  }